<template>
  <div>
    <banner></banner>
    <apresentacao></apresentacao>
    <posts></posts>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./blog/Banner";
import Apresentacao from "./blog/Apresentacao";
import BlogPosts from "./blog/BlogPosts";

export default {
  components: {
    banner: Banner,
    apresentacao: Apresentacao,
    posts: BlogPosts,
  },
  metaInfo() {
    return {
      title: "Blog - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content:
            "No blog Ana Carolina Pereira pode ficar a par de artigos relacionados com a actividade da clínica e da academia!",
        },
        {
          property: "og:title",
          content: "Blog - Ana Carolina Pereira - Clínica de Psicologia",
        },
        {
          property: "og:url",
          content: "https://www.anacarolinapereira.pt/blog",
        },
        {
          property: "og:description",
          content:
            "No blog Ana Carolina Pereira pode ficar a par de artigos relacionados com a actividade da clínica e da academia!",
        },
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
</style>



