<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$green: #91c569;

//paddings and margins
$pd-desktop: 0 390px;
$mg-desktop: 0 40px;

//circle
$circle: 50%;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

.artigos-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
  }

  h1 {
    text-align: center;
  }

  .intro {
    text-align: center;
    font-weight: bold;
  }

  .artigos {
    margin: auto;
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin-top: pxToEm(50);
    max-width: pxToEm(1400);
    width: 100%;

    .ano-wrapper {
      width: 100%;

      @media only screen and (max-width: 767px) {
        max-width: 100%;
      }

      .header {
        height: pxToEm(50);
        background-color: $green;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border-radius: pxToEm(10);

        .text {
          color: $white;
          font-size: pxToEm(20);
          margin-left: pxToEm(20);
        }
      }

      .content {
        display: flex;
        margin: pxToEm(40) 0px;

        .imagem {
          width: pxToEm(230);
          height: pxToEm(300);
          background-color: #cbcbcb;
        }

        .topic {
          margin: 0 0 pxToEm(20) 0;
          overflow: hidden;
          border-bottom: pxToEm(1) solid $dk-gray-3;
          transition: max-height 2s;
          user-select: none;
          width: 100%;

          &:last-child {
            border: none;
          }

          &.open {
            max-height: pxToEm(600);
          }

          .tp {
            height: auto;
            display: flex;
            align-items: center;
            justify-content: space-between;
            // cursor: pointer;

            @media only screen and (max-width: 599px) {
              justify-content: center;
            }

            .text {
              font-size: pxToEm(32);
              font-weight: bold;
              color: #91c569;
            }
          }

          .topic-content {
            display: flex;
            flex-direction: column;
            margin: pxToEm(100) 0;

            @media only screen and (max-width: 767px) {
              flex-direction: row;
              flex-wrap: wrap;
              justify-content: flex-start;
            }

            .copy {
              margin: pxToEm(20) 0;
              font-size: pxToEm(16);
              line-height: pxToEm(30);
              display: flex;

              @media only screen and (max-width: 767px) {
                // margin-right: pxToEm(50);
                width: 100%;
              }

              @media only screen and (max-width: 599px) {
                // margin-right: pxToEm(50);
                width: 100%;
              }

              .text {
                font-size: pxToEm(16);
                display: flex;
                justify-content: space-between;
                width: 100%;

                @media only screen and (max-width: 599px) {
                  flex-direction: column;
                  align-items: center;
                }

                .container {
                  max-width: pxToEm(800);
                  width: 100%;

                  @media only screen and (max-width: 599px) {
                    align-items: center;
                    display: flex;
                    flex-direction: column;

                    > div {
                      text-align: center;
                    }
                  }

                  .title-topic {
                    font-size: pxToEm(34);
                    font-weight: bold;
                  }

                  .date-topic {
                    font-size: pxToEm(28);
                    margin-top: pxToEm(10);
                  }

                  .copy-topic {
                    font-size: pxToEm(24);
                    margin-top: pxToEm(20);
                  }
                }

                a {
                  .img {
                  }
                }
              }
            }
          }
        }
      }
    }

    // .right {
    //   width: 80%;

    //   @media only screen and (max-width: 767px) {
    //     width: 100%;
    //   }
    // }
  }
}
</style>



